"use client"; // RootLayout komponentini client sifatida belgilash

import useAuthStore from "@/stores/MyAuthState"; // Yoki sizning to'g'ri yo'lingiz

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const { token } = useAuthStore(); // Client component ichida chaqirish

  return (
    <html lang="en">
      <body>
        {token ? (
          // Asosiy layoutni ko'rsatish
          <>
            <Header />
            <main>{children}</main>
          </>
        ) : (
          <LoginPage /> // Agar token bo'lmasa, LoginPage ko'rsatilsin
        )}
      </body>
    </html>
  );
}
